import React, { useState } from 'react';
import Collapse from '@mui/material/Collapse';
import '../CSS/Collapse.css';

function MultipleCollapses() {
  const [collapseData, setCollapseData] = useState([
    { id: 1,
      title: 'Mes Comptences en Front-End',
      content:[
        '- intgration web avec html et css.',
        '- intgration web avec Sass.',
        '- intgrer du contenu conformment  une maquette.',
        '- implmenter une interface responsive.',
        '- utilisation de Fontawesome.',
        '- utilisation de Materiel UI.',
        '- suivre le droulement du projet grce  un outil de gestion de projet (trello).',
        '- rdiger les spcifications techniques d\'un projet web  partir des besoins fonctionnels.',
        '- dcouper une fonctionnalit en tches pour prparer le dveloppement.',
        '- prsenter la solution technique.',
        '- mettre en place une mthode de veille technologique.',
        '- versionner son projet avec git et github.',
        '- installer un environnement de dveloppement front-end.',
        '- optimiser les performances d\'un site web.',
        '- dbugger un site web grce aux chrome devtools.',
        '- optimiser l\'accessibilit avec wave evaluation tool.',
        '- rdiger un cahier de recette pour tester un site.',
        '- dveloppement web avec JavaScript:',
        '- rcuprer les donnes utilisateurs dans le JavaScript via des formulaires.',
        '- manipuler les lments du DOM avec JavaScript.',
        '- grer les vnements utilisateurs avec JavaScript.',
        '- configurer la navigation entre les pages de l\'application avec react router.',
        '- initialiser une application avec create react app.',
        '- dvelopper des lments de l\'interface d\'un site web grce  des composants react.',
      ],
      isOpen: false },
    { id: 2,
      title: 'Mes Comptences en Back-End',
      content:[
        '- installer un environnement de dveloppement back-end.', 
        '- implmenter un modle logique de donnes conformment  la rglementation.',
        '- mettre en oeuvre des oprations CRUD de manire scurise.',
        '- stocker des donnes de manire scurise.',
        '- utilisation d\'une base de donne.', 
    ],
      isOpen: false }
  ]);

  const handleToggleCollapse = (id) => {
    setCollapseData((prevData) =>
      prevData.map((collapse) => {
        if (collapse.id === id) {
          return { ...collapse, isOpen: !collapse.isOpen };
        }
        return collapse;
      })
    );
  };

  return (
    <div className='container'>
      {collapseData.map((item) => (
        <div key={item.id}>
          <button className='button_color' onClick={() => handleToggleCollapse(item.id)}>
            {item.title}
          </button>
          <Collapse in={item.isOpen}>
            <ul className='display_li'>
                {item.content.map((el) => (
                  <li key={el}>{el}</li>
                ))}
            </ul>
          </Collapse>
        </div>
      ))}
    </div>
  );
}
export default MultipleCollapses;